.book-container {
    border-style: solid;
    border-width: 2px;
    padding: 1rem;
}

.add-book>form {
    display: flex;
    justify-content: center;
    border-style: none;
}

button {
    border-style: none;
    background-color: white;
    cursor: pointer;
    padding: 0.5rem;
    border-radius: 5px;
    transition: 0.3s;
}

button:hover {
    background-color: #bdbdbd;
    transition: 0.3s;
}

.book-container.card>.add-book {
    display: flex;
    justify-content: center;
    align-items: center;
}


/* row styles */

.row {
    display: flex;
    align-items: center;
    padding: 0.5rem;
    border-style: solid;
    border-width: 2px;
}

.title {
    flex: 1;
}

.author {
    flex: 0.5;
}

.pages {
    flex: 0.2;
}

.trash {
    display: flex;
    align-items: center;
    flex: 0.05;
    cursor: pointer;
}

.checkbox {
    display: flex;
    align-items: center;
    flex: 0.15;
    cursor: pointer;
}

.readStatus {
    flex: 0.1;
}


/* card styles */

.card {
    display: grid;
    border-style: solid;
    border-width: 2px;
    padding: 0.5rem;
}